<template>
<div class="container">
    <div v-if="!loaded" class="loading__screen">
        <div v-for="i in 8" :key="i" v-bind:style="{'--i':i, '--s': `.${i}s`}"><p></p></div>
        <h1>loading . .</h1>
    </div>
        <div class="search__field">
            <span class="shadow"></span>
            <button class="search__button" @click="btnClicked"><img src="../assets/search.svg" alt="search"></button>
            <input type="text" @keyup="inputHandlre" v-model="input1" placeholder="Search">
        </div>
        <router-link v-for="data in animeData" :key="data" :to="{ name:'Information', params: { id: data.mal_id} }" class="routerlink">
            <div class="image"><img :src="data.image_url" :alt="data.title"></div>
            <div>
                <h1 id="title">Title: {{data.title}}</h1>
                <h1>Score: {{data.score}}</h1>
                <h1>Episodes: {{data.episodes}}</h1>
                <!-- <h1 >Genres: {{data.genres}}</h1>    -->
                <h1 class=synopsis>Synopsis: {{data.synopsis}}</h1>
            </div>
        </router-link>
</div>
</template>

<script>
export default {
    data(){
        return {
            animeData: [],
            loaded: false,
            input1: ""
        }
    },
    mounted(){
       fetch("https://jikan1.p.rapidapi.com/genre/anime/1/1", {
        "method": "GET",
        "headers": {
            "x-rapidapi-key": "51b78dd7b4mshc279edfc34eae28p15a677jsna55844206fcc",
            "x-rapidapi-host": "jikan1.p.rapidapi.com" } })
        .then(response => { return response.json() })
        .then(response => {
            this.animeData = response.anime.map((res) => { return res })
            this.loaded = true })
        .catch(err => { console.error(err) });
    },
    methods:{
        inputHandlre(e){ if(e.keyCode==13) this.btnClicked() },
        btnClicked(){
            this.input1= this.input1.trim()
            if(this.input1.length>2){
                const sb = document.getElementsByClassName("search__button")[0]
                sb.classList.add("active")
                fetch(`https://jikan1.p.rapidapi.com/search/anime?q=${this.input1}`, {
                "method": "GET",
                "headers": {
                    "x-rapidapi-key": "51b78dd7b4mshc279edfc34eae28p15a677jsna55844206fcc",
                    "x-rapidapi-host": "jikan1.p.rapidapi.com" } })
                .then(response => { return response.json() })
                .then(response => {
                    this.animeData = response.results.map((item) => {
                        return {
                            mal_id: item.mal_id,
                            title : item.title,
                            image_url : item.image_url, 
                            synopsis : item.synopsis,
                            episodes : item.episodes,
                            score : item.score,
                            } })
                })
                .then(()=>{ sb.classList.remove("active") })
                .catch(err => { console.error(err);});
            }
        }
    }

}
</script>

